.styles {
  transition: margin-left 0.4s ease, padding-left 0.4s ease;
  flex-grow: 1;
  overflow-y: auto;
  outline: none;
  padding: 0 20px 0 0;

  & h1 {
    margin: 0;
    line-height: 1;

    & span {
      color: var(--main-heading-accent-color);
    }
  }

  & table {
    border: 1px solid var(--main-table-border-color);
    border-radius: var(--border-radius);
    width: 100%;
    border-spacing: 0;
    margin-top: 20px;
    overflow: hidden;
    background-color: var(--main-background-color);

    &:global(.associate) td:first-of-type {
      display: none;
    }

    & thead {
      text-transform: uppercase;
      color: var(--main-thead-text-color);
      font-weight: bold;
      font-size: 14px;
      background-color: var(--main-table-thead-background-color);
    }

    & tr:last-child td {
      border-bottom: none;
    }

    & tbody tr:hover {
      background-color: var(--main-hover-background-color);
    }

    & tbody td:nth-child(3) {
      width: 100%;
      white-space: initial;
    }

    & td {
      padding: 10px;
      white-space: nowrap;
      border-bottom: 1px solid var(--main-table-border-color);
    }
  }

  @media (max-width: 799px) {
    padding: 0 20px;
  }
}

.containing {
  font-size: 1.3em;
}

.heading {
  padding-top: 20px;
}

.tableWrap {
  overflow-x: auto;
  outline: none;
  margin-bottom: 20px;
}
