.styles {
  composes: transition-all from global;
  background-color: var(--search-div-background-color);
  padding: 0 20px;
  height: var(--search-div-height);
  display: flex;
  align-items: center;
  border-bottom: var(--search-div-border-bottom);
  flex-shrink: 0;

  @media (min-width: 1000px) {
    padding: 0 240px;
  }
}

.menuButton {
  cursor: pointer;
  padding-right: 20px;

  @media (min-width: 800px) {
    display: none;
  }
}

.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 5px;
  background-color: var(--menu-button-color);
  margin: 6px 0;
  transition: 0.4s ease;
}

:global(.menu-open) {
  & .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }

  & .bar2 {
    opacity: 0;
  }

  & .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }
}

.searchWrap {
  composes: transition-all from global;
  position: relative;
  width: 100%;
  border-radius: var(--border-radius);
  background-color: var(--input-background-color);
  display: flex;
  align-items: center;

  &:hover {
    box-shadow: 0 0 0 2px var(--input-hover-border-color);
  }

  &:focus-within {
    box-shadow: 0 0 0 2px var(--app-focus-border-color);
    background-color: var(--input-focus-background-color);

    & :global(.material-icons.search),
    &.clear :global(.material-icons) {
      color: var(--input-focus-icon-color);
    }

    &.input {
      color: var(--input-focus-text-color);
    }
  }

  & :global(.material-icons) {
    font-size: 30px;
  }

  & :global(.material-icons) {
    color: var(--input-icon-color);

    &:global(.search) {
      padding-left: 10px;
      cursor: default;
    }

    &.clear {
      padding-right: 10px;
      cursor: pointer;
    }
  }
}

.input {
  width: 100%;
  border: none;
  padding: 13px 0;
  background: transparent;
  color: var(--input-text-color);

  &::placeholder {
    color: var(--input-placeholder-text-color);
    opacity: 1;
  }

  &:focus,
  &:active {
    outline: none;

    &::placeholder {
      color: var(--input-focus-placeholder-text-color);
    }
  }
}

.clear {
  margin: 0 10px;
  padding: 0;
  border: none;
  cursor: pointer;
  background: transparent;
  border-radius: var(--border-radius);

  & :global(.material-icons) {
    color: var(--input-icon-color);
    vertical-align: middle;
  }

  &:focus {
    outline: none;
  }

  &:focus-visible {
    box-shadow: 0 0 0 2px var(--app-focus-border-color);
  }
}
