@import "npm:modern-normalize/modern-normalize.css";
@import "./inter-font/inter.css";
@import "./material-icons/material-icons.css";

html,
body,
#app {
  font-family: "Inter", sans-serif;
  height: 100%;
  overflow: hidden;
}

body {
  background-color: var(--app-background-color);

  --app-background-color: #f5f5f5;
  --app-focus-border-color: #2563eb;
  --app-link-hover-text-color: #000;
  --app-link-text-color: #2563eb;
  --app-text-color: #000;
  --border-radius: 4px;
  --main-background-color: #fff;
  --main-heading-accent-color: #2563eb;
  --main-hover-background-color: #f5f5f5;
  --main-table-border-color: #d4d4d4;
  --main-table-thead-background-color: #e5e5e5;
  --main-text-color: #e5e5e5;
  --main-thead-text-color: #525252;
  --menu-button-color: #000;
  --nav-item-active-background-color: #2563eb;
  --nav-item-active-text-color: #fff;
  --nav-item-hover-background-color: #e5e5e5;
  --nav-item-hover-text-color: #000;
  --nav-item-text-color: #000;
  --nav-number-border-bottom: 1px solid #e5e5e5;
  --search-div-background-color: #fff;
  --search-div-border-bottom: 1px solid #e5e5e5;
  --search-div-height: 62px;
  --input-background-color: #f5f5f5;
  --input-focus-background-color: #fff;
  --input-focus-icon-color: #404040;
  --input-focus-placeholder-text-color: #a3a3a3;
  --input-focus-text-color: #000;
  --input-hover-border-color: #e5e5e5;
  --input-icon-color: #a3a3a3;
  --input-placeholder-text-color: #a3a3a3;
  --input-text-color: #000;
}

.transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}
