.styles {
  color: var(--app-text-color);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: 100%;

  & > main {
    display: flex;
    flex-grow: 1;
    overflow: hidden;
  }

  & a {
    color: var(--app-link-text-color);

    &:hover {
      color: var(--app-link-hover-text-color);
    }

    &:focus-visible {
      border-radius: 4px;
      outline: 2px solid var(--app-focus-border-color);
    }
  }
}
