.styles {
  width: 240px;
  flex-shrink: 0;
  padding: 0 20px;

  & ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  & li:not(:last-child) {
    margin-bottom: 5px;
  }

  & li a {
    display: flex;
    padding: 10px;
    border-radius: var(--border-radius);
    background-color: var(--nav-item-background-color);
    color: var(--nav-item-text-color);
    text-decoration: none;
    justify-content: space-between;

    &:hover {
      background-color: var(--nav-item-hover-background-color);
      color: var(--nav-item-hover-text-color);
    }

    &:active {
      filter: brightness(0.9);
    }

    &:global(.active) {
      background-color: var(--nav-item-active-background-color);
      color: var(--nav-item-active-text-color);
    }
  }
}

.count {
  height: 72px;
  display: grid;
  align-items: center;
  text-align: center;
  font-size: 20px;

  & span {
    font-weight: bold;
  }
}

.associate {
  border-top: var(--nav-number-border-bottom);
  padding: 20px 0 10px;
  text-align: center;
  text-transform: uppercase;
}

.settings {
  composes: lightBtn from "../styles/buttons.module.css";
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 5px;
}

@media (max-width: 799px) {
  .styles {
    position: absolute;
    top: 0;
    height: calc(100% - var(--search-div-height));
    margin-top: var(--search-div-height);
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
      0 10px 10px -5px rgba(0, 0, 0, 0.04);
    width: 240px;
    background-color: var(--app-background-color);
    overflow: auto;
    visibility: hidden;
    transform: translateX(-100%);

    & nav {
      border-bottom: var(--nav-number-border-bottom);
      padding-bottom: 13px;
    }

    & .settings {
      width: 100%;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    @nest :global(.menu-open) & {
      visibility: visible;
      transform: translateX(0px);
      transition: transform 0.4s ease-in-out;
    }

    @nest :global(.menu-closing) & {
      visibility: visible;
      transform: translateX(-100%);
      transition: transform 0.4s ease-in-out;
    }
  }
}

@media (min-width: 800px) {
  .styles {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    & .settings {
      margin: 0 0 20px;
    }
  }
}
