.btn {
  padding: 10px;
  border-radius: var(--border-radius);
  border: none;
  background-color: transparent;
  cursor: pointer;

  &:active {
    filter: brightness(0.9);
  }

  &:focus-visible {
    outline: 2px var(--focus-color) solid;
    outline-offset: 1px;
  }
}

.primaryBtn {
  composes: btn;
  --focus-color: #2563eb;
  background-color: #2563eb;
  color: #fff;

  &:hover {
    background-color: #1d4ed8;
  }
}

.secondaryBtn {
  composes: btn;
  --focus-color: #16a34a;
  background-color: #16a34a;
  color: #fff;

  &:hover {
    background-color: #15803d;
  }
}

.standardBtn {
  composes: btn;
  --focus-color: #525252;
  color: #fff;
  background-color: #525252;

  &:hover {
    background-color: #404040;
  }
}

.dangerBtn {
  composes: btn;
  --focus-color: #dc2626;
  background-color: #dc2626;
  color: #fff;

  &:hover {
    background-color: #b91c1c;
  }
}

.lightBtn {
  composes: btn;
  --focus-color: #a3a3a3;
  box-shadow: inset 0 0 0 1px #d4d4d4;
  background-color: #f5f5f5;

  &:hover {
    background-color: #d4d4d4;
  }
}

.clearBtn {
  composes: btn;
  --focus-color: #a3a3a3;
  background-color: transparent;

  &:hover {
    background-color: #d4d4d4;
  }
}

.linkBtn {
  composes: btn;
  --focus-color: #1d4ed8;
  color: #1d4ed8;
  text-decoration: underline;

  &:hover {
    text-decoration: none;
  }

  &:active {
    filter: initial;
  }
}

.closeBtn {
  composes: btn;
  --focus-color: #a3a3a3;
  background-color: transparent;

  &:hover {
    background-color: #d4d4d4;
  }
}
